body {
  display: flex;
  flex-direction: column;
  font-family: 'Cabin';
  min-height: 100vh;
}
form input {
  width: 75%;
  height: 10%;
  padding: 1rem;
  border-radius: 5px;
  border: 1px solid gray;
}
textarea {
  width: 75%;
  padding: 1rem;
  border: 1px solid gray;
  border-radius: 5px;
}
.form {
  display: flex;
  flex-direction: column;
  justify-content: space-evenly;
  align-items: center;
  padding: 1rem;
  gap: 10px;
  margin: auto;
}
.aboutme-card {
  display: flex;
  justify-content: center;
  flex-wrap: wrap;
  margin: auto;
  max-width: 90vw;
  width: auto;
  overflow: hidden;
  word-wrap: break-word;
  padding: 1rem;
  box-sizing: border-box;
}
@media (max-width: 600px) {
  .aboutme-card {
    flex-direction: column;
    text-align: center;
    max-width: 100%;
    padding: 1rem;
    width: 90%;
    box-sizing: border-box;
  }
  .aboutme-card .card-body {
    width: 100%;
  }
}
.card img {
  width: 100%;
  height: 200px;
  object-fit: cover;
}
.card {
  max-width: 20rem;
}
.card-body {
  word-wrap: break-word;
  overflow-wrap: break-word;
}
